@media print {
  *,
  *:before,
  *:after {
    background: transparent !important;
    color: #000 !important;
    /* Black prints faster:
                                   http://www.sanbeiji.com/archives/953 */
    box-shadow: none !important;
    text-shadow: none !important;
  }
  a,
  a:visited {
    text-decoration: underline;
  }
  a[href]:after {
    content: " (" attr(href) ")";
  }
  abbr[title]:after {
    content: " (" attr(title) ")";
  }
  /*
   * Don't show links that are fragment identifiers,
   * or use the `javascript:` pseudo protocol
   */
  a[href^="#"]:after,
  a[href^="javascript:"]:after {
    content: "";
  }
  pre {
    white-space: pre-wrap !important;
  }
  pre,
  blockquote {
    page-break-inside: avoid;
  }
  /*
   * Printing Tables:
   * http://css-discuss.incutio.com/wiki/Printing_Tables
   */
  tr,
  img {
    page-break-inside: avoid;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
  h2,
  h3 {
    page-break-after: avoid;
  }
  .no-print {
    display: none;
  }
  .container {
    padding: 0;
  }
  .resume__photo,
  .resume__info {
    display: inline-block;
    max-width: 47%;
  }
  .resume-body {
    border: none;
  }
}

.icon-ok {
  width: 1em;
  height: 1em;
}

.icon-remove {
  width: 1em;
  height: 1em;
}

* {
  box-sizing: border-box;
}

body {
  color: #4c4c4c;
  font-family: Helvetica, sans-serif;
  font-size: 16px;
}

.container {
  max-width: 980px;
  margin: 0 auto;
  padding: 0 15px;
}

h2 {
  font-size: 16px;
}

blockquote {
  position: relative;
  margin: 0;
  padding: 25px 15px 15px;
  font-style: italic;
  text-align: left;
}

blockquote footer {
  color: #000;
  font-weight: bold;
  text-align: right;
}

blockquote cite {
  font-style: normal;
}

blockquote:before, blockquote:after {
  content: "";
  position: absolute;
  width: 40px;
  height: 40px;
  z-index: -1;
  background: url(../img/quote.png) 0 0 no-repeat;
}

blockquote:before {
  top: 0;
  left: 0;
  transform: rotate(180deg);
}

blockquote:after {
  bottom: 0;
  right: 0;
}

.resume {
  margin: 15px 0;
}

.resume__header {
  background-color: #e6e6e6;
}

@media only screen and (max-width: 767px) {
  .resume__header {
    position: relative;
  }
}

@media only screen and (min-width: 768px) {
  .resume__header {
    display: table;
  }
}

@media only screen and (min-width: 768px) {
  .resume__photo {
    display: table-cell;
    vertical-align: top;
  }
}

.resume__photo-image {
  display: block;
}

.resume__info {
  width: 100%;
  padding: 20px 30px;
  vertical-align: top;
}

@media only screen and (min-width: 768px) {
  .resume__info {
    display: table-cell;
    position: relative;
  }
}

.editable {
  display: inline-block;
  width: 460px;
  max-width: 100%;
  margin: 0 -6px;
  border: 1px solid transparent;
}

.editable__text {
  padding: 0 6px;
  cursor: text;
}

.add-skills__btn {
  padding: 4px 7px;
  cursor: pointer;
}

.add-skills__btn:hover {
  text-decoration: underline;
}

.edited input {
  padding: 0 6px;
  border: none;
}

.edited,
.js-add-skills--clicked {
  position: relative;
}

.edited:focus,
.js-add-skills--clicked:focus {
  background-color: #fff;
}

.edited .editable__text,
.edited .add-skills__btn,
.js-add-skills--clicked .editable__text,
.js-add-skills--clicked .add-skills__btn {
  display: none;
}

.edited input,
.js-add-skills--clicked input {
  display: block;
  position: relative;
  width: 100%;
  outline: none;
  background-color: #fff;
}

.edited {
  border: 1px solid #bababa;
}

.icons__wrap {
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  margin: 0 3px;
}

.icon--revert,
.icon--save {
  display: inline-block;
  width: 22px;
  height: 22px;
  margin: 0 3px;
  color: #e6e6e5;
  transition: 0.3s color ease;
}

.icon--revert:hover,
.icon--save:hover {
  color: #999;
}

.resume__name {
  color: #000;
  font-size: 32px;
}

.editable__text {
  display: inline-block;
}

.resume__country,
.resume__languages {
  margin-top: 3px;
}

.resume__country .editable__text,
.resume__country input,
.resume__languages .editable__text,
.resume__languages input {
  padding: 3px 6px;
  font-size: 15px;
  font-weight: bold;
}

.resume__country input,
.resume__languages input {
  color: #4c4c4c;
}

.resume__country .icon--revert,
.resume__country .icon--save,
.resume__languages .icon--revert,
.resume__languages .icon--save {
  width: 20px;
  height: 20px;
}

.resume__skills {
  margin: 10px -6px;
}

.skill {
  display: inline-block;
  position: relative;
  padding: 3px 9px;
  margin: 4px 3px;
  color: #fff;
  font-size: 12px;
  text-transform: uppercase;
  border-radius: 1px;
}

.skill:hover .remove-skill {
  display: inline-block;
}

.skill--strong {
  background-color: #000;
}

.skill--intermediate {
  background-color: #5c5c5c;
}

.skill--basic {
  background-color: #a2a2a2;
}

.remove-skill {
  display: none;
  position: absolute;
  top: 50%;
  right: 1px;
  margin: -8px 0;
}

.icon-remove {
  width: 16px;
  height: 16px;
  color: #f00;
}

.print-page {
  position: absolute;
  top: 20px;
  right: 30px;
  padding: 3px 15px;
  color: #fff;
  font-size: 12px;
  text-transform: uppercase;
  background-color: #000;
  border-radius: 15px;
  cursor: pointer;
}

.add-skills input {
  padding: 3px 6px;
  border: 1px solid transparent;
}

.add-skills select {
  display: none;
  position: absolute;
  top: 50%;
  right: 33px;
  margin: -10px 0;
  font-size: 12px;
}

.js-add-skills--edited input {
  background-color: #fff;
  border: 1px solid #00ff00;
}

.js-add-skills--edited select {
  display: inline-block;
}

.resume__body {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  border: 1px solid #e6e6e6;
  border-top: none;
}

.resume__body h2 {
  text-align: center;
}

.resume__body ul {
  padding: 0;
  list-style-position: inside;
  text-align: left;
}

.resume-block {
  display: inline-block;
  width: 25%;
  min-height: 237px;
  padding: 20px 10px;
  font-size: 14px;
  border-right: 1px solid #e6e6e6;
  border-top: 1px solid #e6e6e6;
}

.resume-block:nth-of-type(4n) {
  border-right: none;
}

@media only screen and (max-width: 767px) {
  .resume-block {
    width: 100%;
    border-right: none;
  }
}

@media only screen and (max-width: 991px) {
  .resume-block {
    width: 50%;
  }
  .resume-block:nth-of-type(2n) {
    border-right: none;
  }
}

.resume-block h2 {
  margin: 0;
}

.resume-block b {
  font-size: 16px;
}

.resume-block blockquote {
  height: 100%;
}

.resume-block .preferred-environment {
  padding: 0 10px;
}

.resume-block-multiple {
  padding: 0;
}

.resume-block-multiple__item {
  padding: 20px 10px;
}

.resume-block-multiple__item:last-of-type {
  border-top: 1px solid #e6e6e6;
}

.resume-block__desc {
  font-size: 12px;
  text-align: center;
}

.sample-code {
  position: relative;
  padding: 0;
  background: url(../img/code-sample.png);
  overflow: hidden;
}

.sample-code h2 {
  margin: 0;
  padding: 13px 40px;
  background-color: rgba(255, 255, 255, 0.7);
  transform: translateY(0);
  transition: 0.3s transform ease;
}

.sample-code a {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  cursor: pointer;
  text-indent: -9999px;
}

.sample-code:hover h2 {
  transform: translateY(-100%);
}

.resume-block__availability {
  font-size: 30px;
  font-weight: bold;
}
